.header {
  border-radius: 3px;
  position: sticky;
  top: 0;
  margin-bottom: 6px;
  z-index: 5;
}

.header a:hover {
  color: rgb(4, 255, 173) !important;
}

.header input {
  color: black;
}

.lightHeader {
  background-color: rgb(130, 185, 0);
  box-shadow: 0px 2px 4px black;
}

.darkHeader {
  background-color: #161b22;
  box-shadow: 0px 2px 4px rgb(255, 255, 255);
}

.darkHeader * {
  color: white;
}

.bookLogo {
  max-width: 40px;
}

.icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.icon path:hover{
  color: rgb(4, 255, 173) !important;

}

.name {
  margin-left: 15px;
}

.button {
  color: red;
}
