.content{
    padding-top: 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.dark{
    background-color: #1d2431;
    color: white;
}

.dark * {
    color: white;
}

.light{
    background-color: #dce6e3;
}

.sliderWrapper{
    width: 400px !important;
}

@media (max-width: 480px) {
    .sliderWrapper{
        width: 300px !important;
    }
  }

@media (max-width: 360px) {
    .sliderWrapper{
        width: 260px !important;
    }
  }