.image {
  width: 170px;
  height: 240px;
  box-shadow: -5px -6px 10px black;
}

.dark {
  box-shadow: -5px -6px 10px white;
}

.darkLink {
  color: white !important;
}

.darkLink:hover {
  color: rgb(4, 255, 173) !important;
}

.title {
  width: 200px;
  margin-top: 15px;
  border: 1px dotted black;
  color: rgb(252, 106, 1);
}

.item {
  margin-bottom: 30px;
}

.navlink {
  display: block;
  text-decoration: none;
  color: black;
  margin-top: 10px;
  line-height: 20px;
  max-height: 40px;
  overflow: hidden;
}

.navlink:hover {
  color: rgb(25, 147, 163);
}
